.theme-toggle--toggled:not(label).theme-toggle
    .theme-toggle__classic
    :first-child
    path {
    fill: "red";
    stroke: none;
    clip-path: path("M-12 5h30a1 1 0 0 0 9 13v24h-39Z");
    transition-delay: calc(var(--theme-toggle__classic--duration) * 0.2);
}

@supports not (d: path("")) {
    .theme-toggle--toggled:not(label).theme-toggle
        .theme-toggle__classic
        :first-child
        path {
        transform: translate3d(-12px, 10px, 0);
    }
}
.theme-toggle {
    border: none;
    background: 0 0;
    cursor: pointer;
}

.theme-toggle .theme-toggle-sr {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border-width: 0;
    fill: var(--global-fg-color);
}
@media (prefers-reduced-motion: reduce) {
    .theme-toggle:not(.theme-toggle--force-motion) * {
        transition: none !important;
    }
}

.theme-toggle .ert {
    fill: var(--global-fg-color);
}

.theme-toggle.theme-toggle--toggled .ert {
    fill: var(--global-fg-color);
}

.theme-toggle.theme-toggle--reversed .theme-toggle__simple {
    transform: scale(-1, 1);
}
.theme-toggle {
    --theme-toggle__simple--duration: 500ms;
}
.theme-toggle__simple :first-child path {
    transition-property: transform, d;
    transition-duration: calc(var(--theme-toggle__simple--duration));
    transition-timing-function: cubic-bezier(0, 0, 0.15, 1.25);
}





.theme-toggle--toggled:not(label).theme-toggle
    .theme-toggle__simple
    :first-child
    path {
    clip-path: path("M-18-1h55v37h-55zm32 12a1 1 0 0025 0 1 1 0 00-25 0");
}
@supports not (d: path("")) {
    .theme-toggle
        input[type="checkbox"]:checked
        ~ .theme-toggle__simple
        :first-child
        path,
    .theme-toggle--toggled:not(label).theme-toggle
        .theme-toggle__simple
        :first-child
